import React, { Component } from 'react';
import Cubo from './cubo';
import RegistroPortfolio from './registroPortfolio';

const trabajos = require("./trabajos.json");


let cuboClases = ['mostrar-frente', 'mostrar-derecha', 'mostrar-detras', 'mostrar-izquierda', 'mostrar-arriba', 'mostrar-abajo'];
// let nuevoValor;

class Portfolio extends Component {
  constructor() {
    super();
    this.state = { 
      claseSeleccionada: 0,
      trabajos: trabajos
    };
    // fetch("./trabajos.json")
    //   .then(respuesta => respuesta.json())
    //   .then(json => {this.setState({trabajos: json})})
    //   .catch(error => console.log(error));
  }
  // girar = () => {
  //   let claseActual = cuboClases[this.state.claseSeleccionada];
  //   let cubo = document.querySelector('.cubo');
  //   cubo.classList.remove(claseActual);
  //   if (this.state.claseSeleccionada < cuboClases.length - 1)
  //     nuevoValor = this.state.claseSeleccionada + 1;
  //   else
  //     nuevoValor = 0;
  //   cubo.classList.add(cuboClases[nuevoValor]);
  //   this.setState({claseSeleccionada: nuevoValor});
  // }  
  render() {
    let trab = this.state.trabajos;
    let handleClick = (valor) => {
      let claseActual = cuboClases[this.state.claseSeleccionada];
      let cubo = document.querySelector('.cubo');
      cubo.classList.remove(claseActual);
      cubo.classList.add(cuboClases[valor]);
      this.setState({claseSeleccionada: valor});
      var menu = document.getElementById("menu");
      window.scrollTo(0,menu.scrollHeight);
    }
    return (
      <div className='container-fluid'>
        <div className='row'>
          <div className="col-12 col-lg-6 divPortafolioCubo">
            <Cubo trabajos={trab}/>
          </div>
          <div className="col-12 col-lg-6 divPortafolioTabla mb-4">
            <table className="table table-hover">
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Description</th>
                </tr>
              </thead>
              <tbody>
              {
                trab.map(function(registro, index) {
                return (
                      <RegistroPortfolio key={index} registro={registro} onClick={() => handleClick(registro.id)} />
                  );
                })
              }
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }
}

export default Portfolio;