import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import './css/all.min.css';
import './css/fuentes.css';
import './css/App.css';
import './css/cubo.css';
import './css/modalImage.css';
import About from './about';
import Home from './home';
import Portfolio from './portfolio';
import Blog from './blog';
import Contact from './contact';
import Footer from './footer';
import Modal from './modal';
import { Switch, Route, Link } from 'react-router-dom';

var menuSuperior = [
  { nombre: 'Home' , ruta: '/' },
  { nombre: 'About' , ruta: '/about' },
  { nombre: 'Portfolio', ruta: '/portfolio' },
  { nombre: 'Blog', ruta: '/blog' },
  { nombre: 'Contact', ruta: '/contact' }
];

class App extends React.Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <nav id="menu">
            <ul>
              {
                menuSuperior.map(function(registro, index) {
                  return (
                    <li key={index}>
                      <Link to={registro.ruta}>{registro.nombre}</Link>
                    </li>
                  );
                })
              }
            </ul>
          </nav>
        </header>
        <div className='content'>
          <Switch>
            <Route exact path='/' component={Home} />
            <Route path='/about' component={About} />
            <Route path='/portfolio' component={Portfolio}/>
            <Route path='/blog' component={Blog}/>
            <Route path='/contact' component={Contact}/>
          </Switch>
          <Modal/>
        </div>
        <footer>
          <Footer />
        </footer>
      </div>
    );
  }
}

export default App;
